












import { Md5 } from 'md5-typescript'
import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {
  /**data */
  model = {
    userName: '18811112223',
    password: '123456',
  }
  schema = {
    fields: [
      {
        type: "input",
        modelKey: "userName",
        label: "用户名",
        props: {
          placeholer: "请输入用户名",
          clearable: true,
        },
        rules: {
          required: true,
        },
        trigger: "blur",// *待优化：这样会触发多次异步请求校验。思路一：仅当提交表单时才发起一次异步校验
        messages: {
          required: "用户名不能为空",
        },
      },
      {
        type: "input",
        modelKey: "password",
        label: "密码",
        props: {
          placeholer: "请输入密码",
          type: "password",
          eye: {
            open: false
          },
          clearable: true,
          autocomplete: true,
        },
        rules: {
          required: true
        },
        trigger: "blur",
      },
      {
        type: "submit",
        label: "登录"
      }
    ]
  }
  /**method */
  async submitHandler(e: any) {
    e.preventDefault();
    const data = await this.$request({
      api: 'login',
      // url: '/api/3.0/m/login',
      params: {
        userName: this.model.userName,
        password: Md5.init(this.model.password),
        // tagCode: 'INVEST',
        // ip: '',
      },
      method: 'post',
    })
    if (data.resCode === '0000') {
      this.$store.commit('resetAll')
      this.$store.commit('setToken', data.token)
      this.$store.commit('setUser', data.user)

      const redirect = this.$route.query.redirect as string
      if (redirect) { // 重定向
        this.$router.replace(redirect) 
      } else {
        this.$router.push('/project/list')
      }
    }
  }
}
